body {
    font-family: 'Montserrat', sans-serif;
}

.App {
    display: flex;
}

::-webkit-scrollbar {
    width: 8px;
    background-color: #383838;
}
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #6b6b6b;
}
::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: #383838;
}


.content {
    width: 100%;
    height: 100%;
}

.block__title {
    width: 55%;
}

.block__title h2 {
    color: #cdcdcd;
    font-weight: 600;
    font-size: 30px;
}

.block__title span {
    display: block;
    width: 100px;
    height: 2px;
    background: #7198de;
}

@media screen and (max-width: 1536px) {
    .block__title {
        margin-left: 11%;
    }
}

@media screen and (max-width: 1280px) {
    .block__title {
        margin-left: 0;
        width: 90%;
    }
}

@media screen and (max-width: 962px) {
    .block__title {
        width: 86%;
    }
}
.sidebar {
    width: 15%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background: #191d2b;
    border-right: 1px solid #2e344e;
    transition: all .4s ease-out;
    color: #a4acc4;
    overflow: hidden;
    overflow-y: auto;
    position: fixed;
    z-index: 99999999;
}

.sidebar .sidebar__photo {
    padding: 20px 0;
    display: block;
    width: 100%;
    text-align: center;
    border-bottom: 1px solid #2e344e;
}

.sidebar .sidebar__photo img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 7px solid #2e344e;
}

.sidebar .sidebar__navMenu {
    width: 100%;
    text-align: center;
    height: 65%;
    display: flex;
    flex-direction: column;
    align-content: center;
}

.sidebar .sidebar__navMenu ul {
    padding-left: 0;
    margin: auto 0;
}

.sidebar .sidebar__navMenu ul li {
    list-style: none;
    transition: all .4s ease-out;
    cursor: pointer !important;
    text-transform: uppercase;
    font-size: 18px;
    letter-spacing: 1px;
    color: #a4acc4;
    font-weight: 500;
    z-index: 99;
    position: relative;
}
.sidebar .sidebar__navMenu ul li a {
    text-decoration: none;
    color: inherit;
    display: block;
    padding: 10px 0;
}

.sidebar .sidebar__navMenu ul li:not(:last-child) {
    margin-bottom: 1px;
}

.sidebar .sidebar__navMenu ul li:hover {
    background: #7198de;
    color: #ffffff;
}

.sidebar .sidebar__navMenu ul li a.active {
    color: #fff;
    background: cornflowerblue;
}
/*.sidebar .sidebar__navMenu ul li a.active {*/
/*    color: #fff;*/
/*    background: cornflowerblue;*/
/*}*/

.sidebar .sidebar__copyright {
    border-top: 1px solid #2e344e;
    width: 100%;
    height: 5%;
    text-align: center;
}

@media screen and (max-width: 2600px) {
    .sidebar__burger {
        display: none;
    }
    .sidebar .sidebar_close {
        display: none;
    }
}

@media screen and (max-width: 1536px) {
    .sidebar {
        width: 20%;
    }
}

@media screen and (max-width: 1280px) {
    .sidebar {
        left: -300px;
    }
    .sidebar .sidebar_close {
        display: block;
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 999999;
        color: #fff;
        font-weight: 600;
        font-size: 24px;
        cursor: pointer;
    }
    .sidebar__burger {
        display: block;
        position: absolute;
        left: 10px;
        top: 10px;
        z-index: 9999;
        cursor: pointer;
    }
    .sidebar__burger span {
        width: 40px;
        height: 4px;
        background: #fff;
        display: block;
        margin-bottom: 7px;
    }
    .visible_sidebar {
        left: 0;
    }
}

@media screen and (max-width: 1024px) {
    .sidebar {
        width: 25%;
    }
}

@media screen and (max-width: 800px) {
    .sidebar {
        width: 35%;
    }
}

@media screen and (max-width: 640px) {
    .sidebar {
        width: 40%;
    }
}

@media screen and (max-width: 480px) {
    .sidebar {
        width: 50%;
    }
}

@media screen and (max-width: 425px) {
    .sidebar {
        width: 100%;
        left: -425px;
    }
    .visible_sidebar {
        left: 0;
    }
}
.homePage {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    justify-content: center;
    background: #10121b;
}

.homePage .homePage__title {
    color: #ffffff;
    font-size: 30px;
    font-weight: 600;
}

.homePage .homePage__title span {
    color: #7198de;
    font-size: 38px;
}

.homePage .homePage__text {
    color: #cdcdcd;
    font-size: 24px;
    font-weight: 500;
}

@media screen and (max-width: 1536px) {
    .homePage .homePage__title {
        margin-left: 11%;
    }
    .homePage .homePage__text {
        margin-left: 12%;
    }
}

@media screen and (max-width: 1152px) {
    .homePage .homePage__text {
        margin-left: 13%;
        width: 65%;
        text-align: center;
        line-height: 45px;
    }
}

@media screen and (max-width: 1280px) {
    .homePage .homePage__title {
        margin-left: 0;
    }
    .homePage .homePage__text {
        margin-left: 0;
    }
}

@media screen and (max-width: 640px) {
    .homePage .homePage__title {
        text-align: center;
    }
}
.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    justify-content: center;
    background: #10121b;
}

.about .about__text {
    width: 55%;
    color: #cdcdcd;
    margin: 50px auto;
    font-weight: 500;
    font-size: 17px;
}

@media screen and (max-width: 1536px) {
    .about .about__text {
        margin: 0;
        margin-top: 50px;
        margin-left: 16%;
        width: 60%;
    }
}

@media screen and (max-width: 1280px) {
    .about .about__text {
        margin: 0;
        margin-top: 50px;
        margin-left: 0;
        width: 90%;
    }
}

@media screen and (max-width: 962px) {
    .about {
        height: 100vh;
        padding-top: 50px;
    }
    .about .about__text {
        width: 86%;
    }
}

@media screen and (max-width: 640px) {
    .about {
        height: 100%;
        padding-top: 50px;
        padding-bottom: 50px;
    }
}

@media screen and (max-width: 480px) {
    .about {
        height: auto;
        padding-top: 50px;
    }
}
.resume {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    justify-content: center;
    background: #10121b;
    width: 100%;
}

.resume .resume__skills {
    width: 55%;
    margin: 50px auto;
}

.resume__skills_progressBars {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.progressBar {
    width: 48%;
    margin-bottom: 40px;
}

.progressBar .progressBar__title h4 {
    color: #fff;
    font-weight: 600;
    font-size: 20px;
    margin-top: 0;
}

.progressBar .progressBar__inner {
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.progressBar .progressBar__inner .progressBar__inner_procentage {
    color: #fff;
    font-weight: 500;
    font-size: 16px;
    margin-right: 15px;
}

.progressBar .progressBar__inner .progressBar__inner_container {
    background: #2e344e;
    display: block;
    height: 6px;
    width: 100%;
    position: relative;
}

.progressBar .progressBar__inner .progressBar__inner_container span {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    background: #037fff;
    width: 0;
    transition: all 1s cubic-bezier(.01,.57,.68,1.05) 0s;
}

.progressBar .progressBar__inner .progressBar__inner_container .react {
    width: 40%;
}
.progressBar .progressBar__inner .progressBar__inner_container .javascript {
    width: 30%;
}
.progressBar .progressBar__inner .progressBar__inner_container .html {
    width: 70%;
}
.progressBar .progressBar__inner .progressBar__inner_container .css {
    width: 70%;
}
.progressBar .progressBar__inner .progressBar__inner_container .photoshop {
    width: 60%;
}
.progressBar .progressBar__inner .progressBar__inner_container .figma {
    width: 50%;
}


/* ====== Resume ======*/
.resume .resume__info {
    width: 55%;
    margin-top: 100px;
    margin-bottom: 50px;
}

.resume__info_experience {
    border-left: 3px solid #2e344e;
}

.resume__info_experience .experience__block {
    display: flex;
    width: 100%;
    margin-top: 50px;
}

.resume__info_experience .experience__block .experience__block_summary {
    width: 25%;
    padding-left: 20px;
    position: relative;
}
.resume__info_experience .experience__block .experience__block_summary:before {
    content: "";
    position: absolute;
    left: -12px;
    top: 6px;
    height: 15px;
    width: 15px;
    border-radius: 100px;
    background: #10121b;
    border: 3px solid #2e344e;
}
.resume__info_experience .experience__block .experience__block_summary h4 {
    color: #a4acc4;
    margin-bottom: 0;
    font-weight: 600;
    font-size: 18px;
    margin-top: 0;
}

.resume__info_experience .experience__block .experience__block_details {
    position: relative;
    padding-left: 50px;
    width: 75%;
}
.resume__info_experience .experience__block .experience__block_details:before {
    content: "";
    position: absolute;
    left: 0;
    top: 8px;
    height: 1px;
    width: 30px;
    background: #2e344e;
}
.experience__block_details p {
    color: #a4acc4;
    font-weight: 500;
    font-size: 16px;
    margin-top: 0;
}

@media screen and (max-width: 1536px) {
    .resume .resume__skills_title {
        margin-left: 13%;
    }
    .resume .resume__skills {
        width: 60%;
    }
    .resume__skills_progressBars {
        margin-left: 13%;
    }
    .resume__info_title {
        margin-left: 13%;
    }
    .resume .resume__info {
        width: 60%;
    }
    .resume__info_experience {
        margin-left: 13%;
    }
}

@media screen and (max-width: 1280px) {
    .resume .resume__skills {
        width: 90%;
    }
    .resume .resume__skills_title {
        margin-left: 0;
    }
    .resume__skills_progressBars {
        margin-left: 0;
    }
    .resume .resume__info {
        width: 90%;
    }
    .resume__info_title {
        margin-left: 0;
    }
    .resume__info_experience {
        margin-left: 0;
    }
}

@media screen and (max-width: 962px) {
    .resume .resume__skills {
        width: 86%;
    }
    .resume .resume__info {
        width: 86%;
    }
}

@media screen and (max-width: 480px) {
    .progressBar {
        width: 100%;
    }
    .resume .resume__info {
        margin-top: 0;
    }
    .resume__info_experience .experience__block {
        flex-wrap: wrap;
    }
    .resume__info_experience .experience__block .experience__block_summary {
        width: 100%;
        padding-left: 20px;
        padding-top: 7px;
    }
    .resume__info_experience .experience__block .experience__block_details {
        padding-left: 20px;
        margin-top: 15px;
        width: 100%;
    }
    .resume__info_experience .experience__block .experience__block_details:before {
        display: none;
    }
}
